import {useState, useEffect} from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';

const ProdCatDetail = ({match}) => {
    const [categories, setCategory] = useState([])
    // const[notebooks, setNotebooks] = useState([])
    let id = match.params.id

    useEffect(() => {
       axios({
           method: "GET",
           url: `http://localhost:8000/api/prodcategories/`
       })
       .then(response => {
           setCategory(response.data)
           console.log(response.data)
        //    setNotebooks(response.data.notebooks)
       })
    }, [])

    return <>
    {categories.map(c => <div>
        <h5>{c.id}</h5><br></br>
        <Link to={{pathname: `/${c.name}/`, fromDashboard: false}} ><strong>{ c.name }</strong></Link>
        </div>  )}
     
      {/* <ul>
          {notebooks.map(p => <li key={p.id}>
              <p>{p.title}</p><br/><hr/>
              <p>{p.content}</p>
              <Link to={{pathname: `/notebooks/${p.id}/`, fromDashboard: false}}  >Детальнее...</Link>
              </li>)}
      </ul> */}
    </>
}
export default ProdCatDetail